import React, { Component } from 'react';
import Info from './info.js';
import Board from './board.js';
import Loading from './loading.js';
import Modal from './modal.js';
import './App.css';
import axios from 'axios';

class App extends Component {

    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            isWin: false,
            notify: false,
            board: [],
            mines: 0,
            time: 0,
            mineRemain: 0,
            mark: 0
        };
    }

    componentDidMount = () => {}

    setGame(size, mines) {
        this.setState({loading: true, isWin: false, notify: false, time: 0});

    	const board = [];

    	for (let i = 0; i < size; i++) {
    		board.push([]);
    		for (let j = 0; j < size; j++) {
    			board[i].push({
    				isMine: false,
    				value: '',
                    x: j,
                    y: i
    			});
    		}
    	}

    	axios.get('https://tiki-minesweeper.herokuapp.com/getMines', {params: {size: size, mines: mines}}).then((res) => {
    		res['data']['data'].forEach(e => {
    			board[e.y][e.x].isMine = true;
    		});
    		this.setState({board: board, mines: mines, mineRemain: mines, loading: false});
            this.tick();
    	});
    }

    tick() {
        this.intervalID = setInterval(() => {
            this.setState({
                time: this.state.time + 1
            });
        }, 1000);
    }

    checkNumber(y, x) {
    	const { board } = this.state;
    	let num = 0;

        board[y][x].isClick = true;

    	for (let i = y - 1, len = y + 2; i < len; i++) {
    		for (let j = x - 1, leng = x + 2; j < leng; j++) {
    			if (((board[i] || [])[j] || {}).isMine) {
    				num++;
    			}
    		}
    	}

    	if (num) {
            board[y][x].value = num;
        } else {
            board[y][x].value = '';
            const area = this.traverseBoard(y, x);

            console.log(area);

            area.forEach(e => {
                if (!e.isClick && !e.isMine) {
                    this.checkNumber(e.y, e.x);
                }
            });
        }

    	this.setState({board: board});
    }

    traverseBoard(y, x) {
        const { board, mines } = this.state;
        const size = (mines === 10) ? 9 : 16; 
        const el = [];

        if (y > 0) {
            el.push(board[y - 1][x]);
        }

        if (y < size - 1) {
            el.push(board[y + 1][x]);
        }

        if (x > 0) {
            el.push(board[y][x - 1]);
        }

        if (x < size - 1) {
            el.push(board[y][x + 1]);
        }

        if (y > 0 && x > 0) {
            el.push(board[y - 1][x - 1]);
        }

        if (y > 0 && x < size - 1) {
            el.push(board[y - 1][x + 1]);
        }

        if (y < size - 1 && x < size - 1) {
            el.push(board[y + 1][x + 1]);
        }

        if (y < size - 1 && x > 0) {
            el.push(board[y + 1][x - 1]);
        }

        return el;
    }

    checkGame(y, x) {
    	const { board } = this.state;

    	if (board[y][x].isClick) {
    		return false;
    	}

    	if (board[y][x].isMine) {
    		board[y][x].value = 'ðŸ’£';
            clearInterval(this.intervalID);
    		this.setState({board: board, notify: true});
    	} else {
    		this.checkNumber(y, x);
            if (this.checkWin()) {
                clearInterval(this.intervalID);
                this.setState({notify: true, isWin: true});
            }
    	}
    }

    checkWin() {
        const { board, mines } = this.state;
        let conditionWin = (mines === 10) ? 9*9-10 : 16*16-40;
        let numClick = 0;

        for(let i = 0, len = board.length; i < len; i++) {
            for (let j = 0; j < len; j++) {
                if (board[i][j].isClick) {
                    numClick++;
                }
            }
        }

        if (numClick === conditionWin) {
            return true;
        } else {
            return false;
        }
    }

    markCell(y, x, e) {
    	e.preventDefault();
    	const { board, mines } = this.state;
        let { mineRemain, mark } = this.state;

    	if (board[y][x].isClick) {
    		return false;
    	}

    	if (board[y][x].value) {
    		board[y][x].value = '';
            mineRemain = (mark > mines) ? mineRemain : mineRemain + 1;
            mark = mark - 1;
    	} else {
    		board[y][x].value = 'ðŸš©';
            mineRemain = (mineRemain === 0) ? 0 : mineRemain - 1;
            mark = mark + 1;
    	}

    	this.setState({board: board, mineRemain: mineRemain, mark: mark});
    }

    backHomePage() {
        this.setState({
            loading: false,
            isWin: false,
            notify: false,
            board: [],
            mines: 0,
            time: 0,
            mineRemain: 0,
            mark: 0
        });
    }

    render() {
    	const { loading, notify, isWin, board, mines, time, mineRemain } = this.state;

        return (
            <>
                <div className="game">
    	    		{!mines ? (
    	    			<div>
    	    				<button type="submit" onClick={this.setGame.bind(this, 9, 10)}>Beginner</button>
    		    			<button type="submit" onClick={this.setGame.bind(this, 16, 40)}>Advantage</button>
    		    		</div>
    		    	) : ''}
    	    		
    	    		<Info time={time} mineRemain={mineRemain} mines={mines} />
    	    		<Board board={board} mines={mines} check={this.checkGame.bind(this)} mark={(y, x, e) => this.markCell(y, x, e)}/>
    		    </div>
                <Loading loading={loading} />
                <Modal mines={mines} time={time} notify={notify} isWin={isWin} newGame={this.setGame.bind(this)} backHomePage={this.backHomePage.bind(this)} />
            </>
        )
    }
}

export default App;
