import React, { Component } from 'react';

class Modal extends Component {
    constructor(props) {
        super(props);
    }

    componentDidMount = () => {}

    millisToMinutesAndSeconds = ((millis) => {
        let hours = Math.floor(millis / 3600);
        let minutes = Math.floor((millis / 60) % 60);
        let seconds = (millis % 60).toFixed(0);
        return (hours < 10 ? '0' : '') + hours + ":" + (minutes < 10 ? '0' : '') + minutes + ":" + (seconds < 10 ? '0' : '') + seconds;
    });

    render() {
        if (!this.props.notify) {
            return false;
        }

        const size = (this.props.mines === 10) ? 9 : 16; 

        return (
            <div className="messagebox">
                <div className="box">
                    <div className="body">
                        you { !this.props.isWin ? 'lost' : 'win' } the game in {this.millisToMinutesAndSeconds(this.props.time)}
                    </div>
                    
                    <div className="buttons">
                      <button onClick={this.props.newGame.bind(this, size, this.props.mines)}>New Game</button>
                      <button onClick={this.props.backHomePage}>Home Page</button>
                    </div>
                </div>
            </div>
        )
    }
}

export default Modal