import React, { Component } from 'react';

class Board extends Component {
    constructor(props) {
        super(props);
    }

    componentDidMount = () => {}

    render() {
        if (!this.props.mines) {
            return null;
        }

        const cells = this.props.board.map((e, i) => {
            return (
                <div className="clearfix" key={i}>
                    {
                        e.map((o, j) => (
                            <div key={i + '-' + j} className={o.isClick ? 'cell hidden' : 'cell'} onClick={this.props.check.bind(this, i, j)} onContextMenu={this.props.mark.bind(this, i, j)}>{o.value}</div>
                        ))
                    }
                </div>
            )
            
        });

        return (
            <>
                {cells}
            </>
        )
    }
}

export default Board