import React, { Component } from 'react';

class Info extends Component {
    constructor(props) {
        super(props);
    }

    componentDidMount = () => {}

    millisToMinutesAndSeconds = ((millis) => {
        let hours = Math.floor(millis / 3600);
        let minutes = Math.floor((millis / 60) % 60);
        let seconds = (millis % 60).toFixed(0);
        return (hours < 10 ? '0' : '') + hours + ":" + (minutes < 10 ? '0' : '') + minutes + ":" + (seconds < 10 ? '0' : '') + seconds;
    });

    render() {

        if (!this.props.mines) {
            return null;
        }

        return (
            <div className="game-info">
                <span className="info">
                    Mines remaining: {this.props.mineRemain}
                </span>
                <h1 className="info">{this.millisToMinutesAndSeconds(this.props.time)}</h1>
            </div>
        )
    }
}

export default Info